import React from 'react';
import { withRouter } from 'react-router-dom';
import './css/Header.css';
import Menu from './Menu';
import defaultpic from './images/defaultprofilepic.png';

const LogoButton = withRouter(({ history }) => (
    <a href="" id="logo" onClick={() => { history.push('/') }}>myPhotos</a>
))

const UploadButton = withRouter(({ history }) => (
    <a href="" className="turqButton" onClick={() => { history.push('/upload') }}>Upload</a>
))

const LoginButton = withRouter(({ history }) => (
    <a href="" className="turqButton" onClick={() => { history.push('/login') }}>Login</a>
))

const AccountButton = withRouter(({ history }) => (
    <a href="" id="profilepic" onClick={() => { history.push('/account') }}><img src={defaultpic} alt="Go to profile" width="60px"/></a>
))

function Header() {
    return (
        <div>
            <header>
                <section className="header-section">
                    <Menu />
                    <LogoButton />
                </section>

                <section className="header-section">
                    <UploadButton />
                    <LoginButton />
                    <AccountButton />
                </section>
            </header>
        </div>

    );
}

export default Header

