import React from 'react';
import { withRouter } from 'react-router-dom';
import './css/Footer.css';

const LogoButton = withRouter(({ history }) => (
  <a href="" id="logo-footer" onClick={() => { history.push('/') }}>myPhotos</a>
))

const PhotosLink = withRouter(({ history }) => (
  <a href="" onClick={() => { history.push('/photos') }}>Photos</a>
))

const AlbumsLink = withRouter(({ history }) => (
  <a href="" onClick={() => { history.push('/albums') }}>Albums</a>
))

const AccountLink = withRouter(({ history }) => (
  <a href="" onClick={() => { history.push('/account') }}>Account</a>
))

function Footer() {
  return (
    <div>
      <footer>
          <LogoButton />
          <ul>
            <li><PhotosLink /></li>
            <li><AlbumsLink /></li>
            <li><AccountLink /></li>
          </ul>
      </footer>
    </div>
  );
}

export default Footer;

/**References:
 * https://stackoverflow.com/questions/31079081/programmatically-navigate-using-react-router
 */