import React from 'react';
import './css/App.css';
import ScrollToTop from './ScrollToTop';

//Pages 
import Header from './Header';
import Main from './Main';
import Photos from './Photos';
import Albums from './Albums';
import AccountPage from './Account';
import EditProfilePage from './EditProfile';
import LoginPage from './LoginPage';
import SignUpPage from './SignUpPage';
import UploadPage from './UploadImage';
import Footer from './Footer';

import { BrowserRouter, Switch, Route, Link } from "react-router-dom";

/* Links 
<Link to="/">Main</Link>
<Link to="/photos">Photos</Link>
<Link to="/albums">Albums</Link>
<Link to="/account">Account</Link>
<Link to="/upload">Upload</Link>
*/

function App() {
  return (
    <section className="App">
      
      <BrowserRouter>
        <Header />
        <ScrollToTop />

        <section>
          {/*Switch looks through its children Routes and renders the first one that matches the current URL.*/}
          <Switch>
            <Route path="/upload" children={<UploadPage/>} />
            <Route path="/login" children={<LoginPage/>} />
            <Route path="/signup" children={<SignUpPage/>} />
            <Route path="/account" children={<AccountPage/>} />
            <Route path="/editprofile" children={<EditProfilePage/>} />
            <Route path="/albums" children={<Albums/>} />
            <Route path="/photos" children={<Photos/>} />
            <Route path="/" children={<Main/>} />
          </Switch>
        </section>

        <Footer />
      </BrowserRouter>

    </section>
  );
}

export default App;

/** References:
 * https://reactrouter.com/web/guides/quick-start
 */